// 工具 数组去重
function unique(array, isSorted, iteratee) {
  var res = []
  var seen = []

  for (var i = 0, len = array.length; i < len; i++) {
    var value = array[i]
    var computed = iteratee ? iteratee(value, i, array) : value
    if (isSorted) {
      if (!i || seen !== value) {
        res.push(value)
      }
      seen = value
    } else if (iteratee) {
      if (seen.indexOf(computed) === -1) {
        seen.push(computed)
        res.push(value)
      }
    } else if (res.indexOf(value) === -1) {
      res.push(value)
    }
  }
  return res
}

// 对传入的数字进行相关规则进行排序的统一方法
export function sortNumber(areaSplitChar, ballSplitChar, numberList) {
  if (!numberList || !numberList.length) return ''
  if (areaSplitChar === 'j') {
    // todo : 待完善
    // return JSON.stringify(numberList)
    return JSON.stringify(numberList.map(item => `${item.join(ballSplitChar)}`))
  }
  return numberList
    .map(item => {
      return item.join(ballSplitChar)
    })
    .join(areaSplitChar)
}

// 判定参数是否合法且有值
function getParams(params) {
  var betData = params[BET_DATA]
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var resultInfo = {}
  if (!betData) {
    resultInfo.result = -4
    resultInfo.message = '请传入正确格式的betData'
  }
  if (areaSplitChar === undefined) {
    resultInfo.result = -2
    resultInfo.message = "缺少必要的'areaSplitChar' 参数"
  }
  if (ballSplitChar === undefined) {
    resultInfo.result = -1
    resultInfo.message = "缺少必要的'ballSplitChar '参数"
  }
  return {
    result: 0
  }
}

// 核心方法 供外部调用的统一函数
export function checkLotteryCounts(params) {
  var returnData = getParams(params)
  if (returnData.result !== 0) return returnData
  var methodDetail = params[METHOD_DETAIL].split('-') // 1 - 方法类型 2 - 总共行数
  switch (methodDetail[0]) {
    case '1':
      // 表示当前每注需要的号码位数
      params.lineCounts = methodDetail[1]
      // 当前是否是任选模式
      params.isRenxuan = +methodDetail[2]
      // 是否为乐透模式
      params.isLetou = +methodDetail[3]
      return checkLotteryCounts_1(params)
    case '2':
      // 2-3-3-1-3-0
      // 2-6-0-1-0-5
      // 设定当前每注需要的球数
      params.lineCounts = methodDetail[1]
      // 设定是否需要额外的过滤规则
      // 0 -----> 不做处理
      // 1 -----> 过滤多个数字完全一样的情况
      // 2 -----> 过滤多个数字不能完全一样但必须有两个数字相同的情况
      // 3 -----> 过滤要求为三个数字完全不一样
      params.filterLevel = methodDetail[2]
      // 是否需要排序后去重
      params.sortFlag = +methodDetail[3]
      // 是否是任选玩法 不是则为0
      params.isRenxuan = +methodDetail[4]
      // 当前的正则匹配模式: 0 ---> 不带0且无分隔符的0-9的数字判定 (时时彩类)
      //                  1 ---> 小于10的数字带0且以空格作为分隔符的1-11的数字判定 (十一选五类)
      //                  2 ---> 小于10的数字带0且以空格作为分隔符的1-12的数字判定 (快乐十二类)
      //                  3 ---> 小于10的数字带0且以空格作为分隔符的1-20的数字判定 (快乐十分类)
      //                  4 -----> 过滤要求为不能三个数字一样 但可以两个数字相同的情况
      //                  5 -----> 小于10的数字带0且以空格作为分隔符的1-43的数字判定跟一个+N号 N的范围是1 - 16 (双色球)
      params.regType = methodDetail[5]
      return checkLotteryCounts_2(params)
    case '3':
      // 对应的value对象的映射key值
      params.valueType = methodDetail[1]

      // 是否为任选模式 如果0则非任选模式 为数字则是为任选x
      // 这里接受的是玩法中的任选模式 并非用户选择任选模式
      params.renxuanInMenuDetail = +methodDetail[2]
      return checkLotteryCounts_3(params)
    case '4':
      // 需要当前每注需要的球数
      //4-4-0
      params.singleNoteBalls = methodDetail[1]
      // 需要乘以的系数
      params.multiplicationCount = +methodDetail[2]

      // 是否为任选模式 如果0则非任选模式 为数字则是为任选x
      // 这里接受的是玩法中的任选模式 并非用户选择任选模式
      params.renxuanInMenuDetail = +methodDetail[3]
      return checkLotteryCounts_4(params)
    case '5':
      //5-0-0-1-1-0
      // 不需要被组合的行数
      params.repeatIndex = methodDetail[1]

      // 计算类型
      params.calculationType = methodDetail[2]

      // 不需要被组合行数的单注所需球数
      params.repeatBalls = methodDetail[3] ? methodDetail[3] : 0

      // 需要被组合行数的单注所需球数
      params.noRepeatBalls = methodDetail[4] ? methodDetail[4] : 0

      // 是否为任选模式 如果0则非任选模式 为数字则是为任选x
      // 这里接受的是玩法中的任选模式 并非用户选择任选模式
      params.renxuanInMenuDetail = +methodDetail[5]

      // 是否允许上下方的小球数字一样
      params.isAllowRepeat = !!+methodDetail[6]
      return checkLotteryCounts_5(params)
    case '6':
      params.isLetou = +methodDetail[1]
      return checkLotteryCounts_6(params)
    case '7':
      // 胆码的数量
      params.danCounts = +methodDetail[1]
      return checkLotteryCounts_7(params)
  }
}

