// 复式类
function checkLotteryCounts_1(params) {
  var betStrData = params[BET_DATA].slice()
  var betData = params[BET_DATA].filter(item => item.length)
  var selectLineCounts = betData.length
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var isRenxuan = params.isRenxuan
  var isLetou = params.isLetou
  if (isRenxuan === undefined) {
    return {
      result: -5,
      message: "'methodDetail'字段不完整",
      betCount: 0,
      numberStr: '',
      selectLineCounts: 0,
      showNumberCountsStr: 0
    }
  }
  var betCount
  if (isRenxuan) {
    betCount = 0
    switch (isRenxuan) {
      case 2:
        for (var i = 0; i < betData.length; i++) {
          var remainCount = 0
          for (var j = 0; j < betData.length; j++) {
            if (j <= i) continue
            remainCount += betData[j].length
          }
          betCount += remainCount * betData[i].length
        }
        break
      case 3:
        var newBetDataArr = betData.filter(item => item.length)
        if (newBetDataArr.length === 3) {
          betCount =
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length
        }
        if (newBetDataArr.length === 4) {
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
        }
        if (newBetDataArr.length === 5) {
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[2].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[1].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[2].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
        }
        if (newBetDataArr.length < 3) betCount = 0
        break
      case 4:
        var newBetDataArr = betData.filter(item => item.length)
        if (newBetDataArr.length === 4) {
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
        }
        if (newBetDataArr.length === 5) {
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[0].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
          betCount +=
            newBetDataArr[1].length *
            newBetDataArr[2].length *
            newBetDataArr[3].length *
            newBetDataArr[4].length
        }
        if (newBetDataArr.length < 4) betCount = 0
        break
      default:
        return {
          result: -6,
          message: '未被定义的任选项, methodDetail的下标2配置错误',
          betCount: 0,
          numberStr: '',
          selectLineCounts: 0,
          showNumberCountsStr: 0
        }
    }
  } else {
    if (betData.length < params.lineCounts) {
      betCount = 0
    } else {
      if (isLetou) {
        betCount = 0
        switch (params.lineCounts) {
          case '3':
            for (var i = 0; i < betData[0].length; i++) {
              for (var j = 0; j < betData[1].length; j++) {
                if (betData[0][i] === betData[1][j]) continue
                for (var k = 0; k < betData[2].length; k++) {
                  if (
                    betData[2][k] === betData[0][i] ||
                    betData[2][k] === betData[1][j]
                  )
                    continue
                  betCount += 1
                }
              }
            }
            break
          case '2':
            for (var i = 0; i < betData[0].length; i++) {
              for (var j = 0; j < betData[1].length; j++) {
                if (betData[0][i] === betData[1][j]) continue
                betCount += 1
              }
            }
            break
        }
      } else {
        var countArr = []
        for (var i = 0; i < betData.length; i++) {
          countArr.push(betData[i].length)
        }
        betCount = countArr.reduce(
          (accumulator, currentValue) => accumulator * currentValue
        )
      }
    }
  }
  return {
    result: 0,
    message: '',
    betCount: betCount,
    selectLineCounts: selectLineCounts,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betStrData),
    showNumberCountsStr: 0
  }
}

// 单式类
function checkLotteryCounts_2(params) {
  // 2-3-3-1-0-0
  var isCreateByRandom = params.isCreateByRandom
  var betData = params[BET_DATA]
  var rxCounts = params[RX_COUNTS]
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var numberCountLimit = +params[NUMBER_COUNT_LIMIT]
  var filterLevel = params.filterLevel
  var sortFlag = params.sortFlag
  var isRenxuan = params.isRenxuan
  var showNumberCountsStr = 0

  // /(\s|\b|\D)(\d{3})(,|\s|;|\b|\D)/g
  var regExp
  var isRepeatNumber = false
  var splitChar = ''

  if (isCreateByRandom) {
    var numberRandom = ''
    var randomInitArr = []
    var randomInnerArr = []
    var randomCounts = params.lineCounts
    var returnStr = ''
    // 做相同数字组合的重复去除处理程序
    if (params.regType === '0') {
      // 时时彩类型
      for (var randomIndex = 0; randomIndex < isCreateByRandom; randomIndex++) {
        while (randomCounts--) {
          numberRandom = Math.round(Math.random() * 9)
          // 时时彩的单式过滤规则
          // 0 -----> 不做处理
          // 1 -----> 过滤多个数字完全一样的情况
          // 2 -----> 过滤多个数字不能完全一样但必须有两个数字相同的情况
          // 3 -----> 过滤要求为三个数字完全不一样
          // 4 -----> 过滤要求为不能三个数字一样
          if (
            randomInnerArr.length === params.lineCounts - 1 &&
            filterLevel === '1'
          ) {
            var filterCountsRandom = params.lineCounts - 1
            var regStrRandom = '(\\d)'
            while (filterCountsRandom--) {
              regStrRandom += '\\1'
            }
            if (
              new RegExp(regStr, 'g').test(
                randomInnerArr.join('') + numberRandom
              )
            ) {
              // 不符合规则 多个数字完全一样 重新生成最后一个数字
              randomCounts++
              continue
            }
          }
          if (
            randomInnerArr.length === params.lineCounts - 1 &&
            filterLevel === '2'
          ) {
            var filterLevel2Str = randomInnerArr.join('') + numberRandom
            if (/(\d)\1\1/g.test(filterLevel2Str)) {
              // 三个完全一样的时候
              randomCounts++
              continue
            } else if (
              /(\d)\1\d/g.test(filterLevel2Str) ||
              /(\d)\d\1/g.test(filterLevel2Str) ||
              /\d(\d)\1/g.test(filterLevel2Str)
            ) {
              // 两个一样的情况下
            } else {
              // 三个完全不一样的情况
              numberRandom =
                Math.random() > 0.5 ? randomInnerArr[0] : randomInnerArr[1]
            }
          }
          if (
            randomInnerArr.length === params.lineCounts - 1 &&
            filterLevel === '3'
          ) {
            var filterLevel3Str = randomInnerArr.join('') + numberRandom
            // 当发生两个一样的情况下 重新生成数字
            if (
              /(\d)\1/g.test(filterLevel3Str) ||
              /(\d)\d\1/g.test(filterLevel3Str)
            ) {
              randomInnerArr = []
              randomCounts = +params.lineCounts
              continue
            }
          }
          if (
            randomInnerArr.length === params.lineCounts - 1 &&
            filterLevel === '4'
          ) {
            if (/(\d)\1\1/g.test(randomInnerArr.join('') + numberRandom)) {
              randomCounts++
              continue
            }
          }
          randomInnerArr.push(numberRandom)
        }
        if (
          sortFlag &&
          randomInitArr
            .map(item => item.sort((a, b) => +a - +b).join(''))
            .find(
              item => item === randomInnerArr.sort((a, b) => +a - +b).join('')
            )
        ) {
          randomIndex--
        } else {
          randomInitArr.push(randomInnerArr.sort((a, b) => +a - +b))
        }
        randomInnerArr = []
        randomCounts = params.lineCounts
      }
    }
    if (params.regType === '1') {
      // 十一选五类型
      for (
        var random2Index = 0;
        random2Index < isCreateByRandom;
        random2Index++
      ) {
        while (randomCounts--) {
          numberRandom = Math.round(Math.random() * 10 + 1)

          if (randomInnerArr.find(item => +item === +numberRandom)) {
            randomCounts++
            continue
          }

          randomInnerArr.push(numberRandom)
        }
        if (
          filterLevel === '1' &&
          randomInitArr
            .map(item => item.sort((a, b) => +a - +b).join(''))
            .find(item => item === randomInnerArr.join(''))
        ) {
          random2Index--
        } else {
          randomInitArr.push(randomInnerArr.sort((a, b) => +a - +b))
        }
        randomInnerArr = []
        randomCounts = params.lineCounts
      }
    }
    if (params.regType === '2') {
      // 快乐十二
      for (
        var random3Index = 0;
        random3Index < isCreateByRandom;
        random3Index++
      ) {
        while (randomCounts--) {
          numberRandom = Math.round(Math.random() * 11 + 1)

          if (randomInnerArr.find(item => +item === +numberRandom)) {
            randomCounts++
            continue
          }

          randomInnerArr.push(numberRandom)
        }
        if (
          filterLevel === '1' &&
          randomInitArr
            .map(item => item.sort((a, b) => +a - +b).join(''))
            .find(item => item === randomInnerArr.join(''))
        ) {
          random3Index--
        } else {
          randomInitArr.push(randomInnerArr.sort((a, b) => +a - +b))
        }
        randomInnerArr = []
        randomCounts = params.lineCounts
      }
    }
    if (params.regType === '3') {
      // 快乐十二
      for (
        var random4Index = 0;
        random4Index < isCreateByRandom;
        random4Index++
      ) {
        while (randomCounts--) {
          numberRandom = Math.round(Math.random() * 19 + 1)

          if (randomInnerArr.find(item => +item === +numberRandom)) {
            randomCounts++
            continue
          }

          randomInnerArr.push(numberRandom)
        }
        if (
          filterLevel === '1' &&
          randomInitArr
            .map(item => item.sort((a, b) => +a - +b).join(''))
            .find(item => item === randomInnerArr.join(''))
        ) {
          random4Index--
        } else {
          randomInitArr.push(randomInnerArr.sort((a, b) => +a - +b))
        }
        randomInnerArr = []
        randomCounts = params.lineCounts
      }
    }
    if (params.regType === '5') {
      // 双色球
      for (
        var random5Index = 0;
        random5Index < isCreateByRandom;
        random5Index++
      ) {
        while (randomCounts--) {
          numberRandom =
            randomCounts === 1
              ? Math.round(Math.random() * 42 + 1)
              : Math.round(Math.random() * 15 + 1)

          if (
            randomInnerArr.find((item, index) => {
              if (item === +numberRandom && index !== 5) {
                return true
              }
            })
          ) {
            randomCounts++
            continue
          }

          if (randomCounts === 1) {
            randomInnerArr = randomInnerArr.sort((a, b) => +a - +b)
          }
          randomInnerArr.push(numberRandom)
        }
        randomInitArr.push(randomInnerArr)
        randomInnerArr = []
        randomCounts = params.lineCounts
      }
    }
    if (['1', '2', '3', '4', '5'].includes(params.regType)) {
      randomInitArr = randomInitArr.map(item =>
        item.map((itemInner, indexInner) => {
          if (params.regType === '5' && indexInner === item.length - 1) {
            var returnItem = itemInner < 10 ? '0' + itemInner : itemInner
            return '+' + returnItem
          }
          return itemInner < 10 ? '0' + itemInner : itemInner
        })
      )
      if (params.regType === '5') {
        returnStr = randomInitArr
          .map(item => {
            return item.slice(0, 5).join(' ') + item[item.length - 1]
          })
          .join('\n')
      } else {
        returnStr = randomInitArr
          .map(item => {
            return item.join(' ')
          })
          .join('\n')
      }
    } else {
      returnStr = randomInitArr
        .map(item => {
          return item.join('')
        })
        .join('\n')
    }
    return {
      result: 0,
      message: '',
      sourceData: randomInitArr,
      numberStr: returnStr
    }
  } else {
    switch (params.regType) {
      case '0':
        splitChar = /,|;|\s|\|/g
        regExp = new RegExp(
          '(\\s|\\b|\\D)(\\d{' + params.lineCounts + '})(,|\\s|;|\\b|\\D)'
        )
        break
      case '1':
        splitChar = /,|;|\|/g
        var lineCountsReg = params.lineCounts - 1
        var regStrInType2 = ''
        while (lineCountsReg--) {
          regStrInType2 += '(\\d{1,2})\\s'
        }
        regStrInType2 += '(\\d{1,2}(?!\\d))(,|;|)'
        regExp = new RegExp(regStrInType2)
        break
      case '2':
        splitChar = /,|;|\|/g
        var lineCounts3Reg = params.lineCounts - 1
        var regStrInType3 = ''
        while (lineCounts3Reg--) {
          regStrInType3 += '(\\d{1,2})\\s'
        }
        regStrInType3 += '(\\d{1,2}(?!\\d))(,|;|)'
        regExp = new RegExp(regStrInType3)
        break
      case '3':
        splitChar = /,|;|\|/g
        var lineCounts4Reg = params.lineCounts - 1
        var regStrInType4 = ''
        while (lineCounts4Reg--) {
          regStrInType4 += '(\\d{1,2})\\s'
        }
        regStrInType4 += '(\\d{1,2}(?!\\d))(,|;|)'
        regExp = new RegExp(regStrInType4)
        break
      case '5':
        splitChar = /,|;|\||\n/g
        var lineCounts5Reg = params.lineCounts - 2
        var regStrInType5 = ''
        while (lineCounts5Reg--) {
          regStrInType5 += '(\\d{1,2})\\s'
        }
        regStrInType5 += '(\\d{1,2})\\+(\\d{1,2}(?!\\d))(,|;|)'
        regExp = new RegExp(regStrInType5)
        break
      default:
        return {
          result: -6,
          message: '未被定义的任选项, methodDetail的下标5配置错误',
          betCount: 0,
          numberStr: '',
          selectLineCounts: '',
          showNumberCountsStr: 0
        }
    }
    // var strArr = betData.match(regExp) || []
    splitChar = splitChar || ''
    var initData = betData.replace(splitChar, ';').split(';')
    var strArr = initData
      .filter((item, index) => {
        if (index !== initData.length - 1 && !regExp.test(item)) {
          isRepeatNumber = true
        }
        return regExp.test(item)
      })
      .map(item => item.replace(/(^\s*)|(\s*$)/g, ''))

    // 做相同数字组合的重复去除处理程序
    if (sortFlag && params.regType === '0') {
      var newSortArr = []
      for (var i = 0; i < strArr.length; i++) {
        var replaceItem = strArr[i]
          .replace(/[^\d]/g, '')
          .split('')
          .sort((a, b) => +a - +b)
          .join('')
        newSortArr.push(replaceItem)
        if (
          !isRepeatNumber &&
          newSortArr.find((repeatItem, repeatIndex) => {
            return repeatItem === replaceItem && repeatIndex !== i
          })
        )
          isRepeatNumber = true
      }
      strArr = unique(newSortArr)
    }
    if (sortFlag && params.regType === '1') {
      // 过滤乐透玩法的单注重复球数
      strArr = strArr.filter(item => {
        const numberArr = item.replace(/[^\d|\s]/, '').split(' ')
        return (
          !numberArr.find(itemInner => {
            return +itemInner <= 0 || +itemInner > 11
          }) &&
          !numberArr.find((item, index) => {
            if (
              numberArr.find(
                (itemInner, indexInner) =>
                  item === itemInner && index !== indexInner
              )
            ) {
              isRepeatNumber = true
              return true
            }
          })
        )
      })
      var arr = []
      for (var i = 0; i < strArr.length; i++) {
        var strItem = strArr[i].replace(/[^\d|\s]/, '').split(' ')
        var arrInner = []
        for (var j = 0; j < strItem.length; j++) {
          if (+strItem[j] < 0 || +strItem[j] > 11) break
          arrInner.push(
            Number(strItem[j]) < 10 ? `0${+strItem[j]}` : strItem[j]
          )
        }
        if (arr.find(arrItem => arrItem === arrInner.join(' ')))
          isRepeatNumber = true
        if (arrInner.length === +params.lineCounts) arr.push(arrInner.join(' '))
      }
      strArr = unique(arr)
    }
    if (sortFlag && params.regType === '2') {
      // 过滤乐透玩法的单注重复球数
      strArr = strArr.filter(item => {
        const numberArr = item.replace(/[^\d|\s]/, '').split(' ')
        return (
          !numberArr.find(itemInner => {
            return +itemInner < 1 || +itemInner > 12
          }) &&
          !numberArr.find((item, index) => {
            if (
              numberArr.find(
                (itemInner, indexInner) =>
                  item === itemInner && index !== indexInner
              )
            ) {
              isRepeatNumber = true
              return true
            }
          })
        )
      })
      var arr3 = []
      for (var i = 0; i < strArr.length; i++) {
        var str3Item = strArr[i].replace(/[^\d|\s]/, '').split(' ')
        var arr3Inner = []
        for (var j = 0; j < str3Item.length; j++) {
          if (+str3Item[j] < 0 || +str3Item[j] > 12) break
          arr3Inner.push(
            Number(str3Item[j]) < 10 ? `0${+str3Item[j]}` : str3Item[j]
          )
        }
        if (arr3.find(arrItem => arrItem === arr3Inner.join(' ')))
          isRepeatNumber = true
        if (arr3Inner.length === +params.lineCounts)
          arr3.push(arr3Inner.join(' '))
      }
      strArr = unique(arr3)
    }
    if (sortFlag && params.regType === '3') {
      // 过滤乐透玩法的单注重复球数
      strArr = strArr.filter(item => {
        const numberArr = item.replace(/[^\d|\s]/, '').split(' ')
        return (
          !numberArr.find(itemInner => {
            return +itemInner < 1 || +itemInner > 20
          }) &&
          !numberArr.find((item, index) => {
            return numberArr.find((itemInner, indexInner) => {
              if (item === itemInner && index !== indexInner) {
                isRepeatNumber = true
                return true
              }
            })
          })
        )
      })
      var arr4 = []
      for (var i = 0; i < strArr.length; i++) {
        var str4Item = strArr[i].replace(/[^\d|\s]/, '').split(' ')
        var arr4Inner = []
        for (var j = 0; j < str4Item.length; j++) {
          if (+str4Item[j] < 0 || +str4Item[j] > 20) break
          arr4Inner.push(
            Number(str4Item[j]) < 10 ? `0${+str4Item[j]}` : str4Item[j]
          )
        }
        if (arr4.find(arrItem => arrItem === arr4Inner.join(' ')))
          isRepeatNumber = true
        if (arr4Inner.length === +params.lineCounts)
          arr4.push(arr4Inner.join(' '))
      }
      strArr = unique(arr4)
    }
    if (sortFlag && params.regType === '5') {
      // 双色球
      strArr = strArr.filter(item => {
        const numberArr = item
          .replace('+', ' ')
          .replace(/[^\d|\s]/, '')
          .split(' ')
        return (
          !numberArr.find((itemInner, indexInner) => {
            return (
              ((+itemInner < 1 || +itemInner > 43) && indexInner < 5) ||
              (indexInner === 5 && (+itemInner < 1 || +itemInner > 16))
            )
          }) &&
          !numberArr.find((item, index) => {
            return numberArr.find((itemInner, indexInner) => {
              if (
                item === itemInner &&
                index !== indexInner &&
                indexInner !== 5 &&
                index !== 5
              ) {
                isRepeatNumber = true
                return true
              }
            })
          })
        )
      })
      var arr5 = []
      for (var i = 0; i < strArr.length; i++) {
        var str5Item = strArr[i]
          .replace('+', ' ')
          .replace(/[^\d|\s]/, '')
          .split(' ')
        var arr5Inner = []
        for (var j = 0; j < str5Item.length; j++) {
          if (+str5Item[j] < 1 || +str5Item[j] > 43) break
          arr5Inner.push(
            Number(str5Item[j]) < 10 ? `0${+str5Item[j]}` : str5Item[j]
          )
          // if (j === 5) str5Item[j] = '+' + str5Item[j]
        }
        if (arr5.find(arrItem => arrItem === arr5Inner.join(' ')))
          isRepeatNumber = true
        if (arr5Inner.length === +params.lineCounts)
          arr5.push(arr5Inner.join(' '))
      }
      strArr = unique(arr5)
    }
    // 设定是否需要额外的过滤规则
    if (params.regType === '0') {
      // 时时彩的单式过滤规则
      // 0 -----> 不做处理
      // 1 -----> 过滤多个数字完全一样的情况
      // 2 -----> 过滤多个数字不能完全一样但必须有两个数字相同的情况
      // 3 -----> 过滤要求为三个数字完全不一样
      // 4 -----> 过滤要求为不能三个数字一样 但可以两个数字相同的情况
      if (filterLevel === '1') {
        var filterCounts = params.lineCounts - 1
        var regStr = '(\\d)'
        while (filterCounts--) {
          regStr += '\\1'
        }
        var regStrFilter = new RegExp(regStr, 'g')
        strArr = strArr.filter(item => {
          return !regStrFilter.test(item)
        })
      } else if (filterLevel === '2') {
        strArr = strArr.filter(item => {
          // item = item.join('')
          return (
            !/(\d)\1\1/g.test(item) &&
            (/(\d)\1\d/g.test(item) ||
              /(\d)\d\1/g.test(item) ||
              /\d(\d)\1/g.test(item))
          )
        })
      } else if (filterLevel === '3') {
        strArr = strArr.filter(item => {
          // item = item.join('')
          return !/(\d)\1/g.test(item) && !/(\d)\d\1/g.test(item)
        })
      } else if (filterLevel === '4') {
        strArr = strArr.filter(item => {
          // item = item.join('')
          return !/(\d)\1\1/g.test(item)
        })
      }
      var uniqueArr = unique(
        strArr.map(item => {
          return item.replace(/[^\d]/g, '')
        })
      )
      if (uniqueArr.length !== strArr.length) {
        isRepeatNumber = true
      }

      strArr = uniqueArr.map(item => {
        return item.split('')
      })
      // 性能消耗较大 需更换
      /* strArr = strArr.filter((item, index) => {
        item = item.join('')
        return strArr.find((itemInner, indexInner) => {
          itemInner = itemInner.join('')
          return itemInner === item && indexInner === index
        })
      }) */
    } else if (
      params.regType === '1' ||
      params.regType === '2' ||
      params.regType === '3'
    ) {
      // 十一选五的单式过滤规则
      // 0 ------> 不做处理(但是已经对完全相同的数字进行了一套过滤)
      // 1 ------> 过滤掉排序后数字一样的投注串
      if (filterLevel === '1') {
        var filterRepeatArr = strArr.map(item => {
          return item
            .replace(/[^\d|\s]/, '')
            .split(' ')
            .map(itemInner => {
              return Number(itemInner) < 10 ? `0${+itemInner}` : itemInner
            })
            .sort((a, b) => +a - +b)
            .join(',')
        })
        filterRepeatArr.find((item, index) => {
          return filterRepeatArr.find((itemInner, indexInner) => {
            if (item === itemInner && index !== indexInner) {
              isRepeatNumber = true
              return true
            }
          })
        })
        strArr = unique(filterRepeatArr)
        strArr = strArr.map(item => item.split(','))
      } else {
        strArr = strArr.map(item => item.split(' '))
      }
    } else if (params.regType === '5') {
      strArr = strArr.map(item => item.split(' '))
    }

    var isMore = false
    if (numberCountLimit) isMore = strArr.length > numberCountLimit
    if (isMore) strArr = strArr.slice(0, numberCountLimit)
    var strArrLength = strArr.length
    if (isRenxuan) {
      showNumberCountsStr = strArrLength * combination(rxCounts, isRenxuan)
    } else {
      showNumberCountsStr = strArrLength
    }
    if (isMore)
      return {
        result: -7,
        message:
          '超出最大注数限制, 当前玩法最高限制注数为' + numberCountLimit + '注',
        isRepeatNumber: isRepeatNumber,
        selectLineCounts: '',
        betCount: showNumberCountsStr,
        sourceData: strArr,
        numberStr: sortNumber(areaSplitChar, ballSplitChar, strArr),
        showNumberCountsStr: showNumberCountsStr
      }
    else
      return {
        result: 0,
        message: '',
        sourceData: strArr,
        selectLineCounts: '',
        isRepeatNumber: isRepeatNumber,
        betCount: showNumberCountsStr,
        numberStr: sortNumber(areaSplitChar, ballSplitChar, strArr),
        showNumberCountsStr: showNumberCountsStr
      }
  }
}

// 和值 跨度类
function checkLotteryCounts_3(params) {
  var betData = params[BET_DATA]
  var selectLineCounts = (
    betData.find(item => {
      return item.length > 0
    }) || []
  ).length
  if (!selectLineCounts) {
    return {
      result: 0,
      message: '',
      betCount: 0,
      numberStr: '',
      selectLineCounts: 0,
      showNumberCountsStr: 0
    }
  }
  var rxCounts = params[RX_COUNTS]
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var valueType = params.valueType
  var renxuanInMenuDetail = params.renxuanInMenuDetail
  var count = 0
  var numbers = betData[0]
  if (typeof sumValueMap[valueType] === 'object') {
    for (var i = 0; i < numbers.length; i++) {
      count += +sumValueMap[valueType][numbers[i]]
    }
  } else if (typeof sumValueMap[valueType] === 'number') {
    for (var i = 0; i < numbers.length; i++) {
      count += +sumValueMap[valueType]
    }
  } else {
    return {
      result: -6,
      message: '未被定义的任选项, methodDetail的下标3配置错误',
      betCount: 0,
      numberStr: '',
      selectLineCounts: '',
      showNumberCountsStr: 0
    }
  }

  if (rxCounts) {
    count = count * combination(rxCounts, renxuanInMenuDetail)
  }
  return {
    result: 0,
    message: '',
    betCount: count,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betData),
    selectLineCounts: selectLineCounts,
    showNumberCountsStr: count
  }
}

// 组选 单行类
function checkLotteryCounts_4(params) {
  var betData = params[BET_DATA]
  var selectLineCounts = betData.filter(item => item.length).length
  if (!betData.length || !betData[0].length) {
    return {
      result: 0,
      message: '',
      betCount: 0,
      numberStr: '',
      showNumberCountsStr: 0,
      selectLineCounts: selectLineCounts
    }
  }
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var rxCounts = params[RX_COUNTS]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var multiplicationCount = +params.multiplicationCount
    ? params.multiplicationCount
    : 1
  var renxuanInMenuDetail = params.renxuanInMenuDetail
  var count =
    combination(betData[0].length, +params['singleNoteBalls']) *
    multiplicationCount
  if (renxuanInMenuDetail) {
    count = count * combination(rxCounts, renxuanInMenuDetail)
  }
  return {
    result: 0,
    message: '',
    betCount: count,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betData),
    showNumberCountsStr: count,
    selectLineCounts: selectLineCounts
  }
}

// 组选 重号类
function checkLotteryCounts_5(params) {
  var betData = params[BET_DATA]
  var repeatIndex = +params.repeatIndex
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var rxCounts = params[RX_COUNTS]
  var selectLineCounts = betData.filter(item => item.length).length
  var renxuanInMenuDetail = params.renxuanInMenuDetail
  var isAllowRepeat = params.isAllowRepeat
  var repeatArr
  var singleArr
  var count = 0
  if (betData.length === 2) {
    for (var i = 0; i < betData.length; i++) {
      if (repeatIndex === i) {
        repeatArr = betData[i]
        continue
      }
      singleArr = betData[i]
    }
  }

  if (repeatArr.length && singleArr.length) {
    switch (params.calculationType) {
      case '0':
        for (var i = 0; i < repeatArr.length; i++) {
          var actualArr = []
          for (var j = 0; j < singleArr.length; j++) {
            if (isAllowRepeat) {
              actualArr.push(singleArr[j])
              continue
            }
            if (singleArr[j] !== repeatArr[i]) {
              actualArr.push(singleArr[j])
            }
          }
          count += combination(actualArr.length, +params.noRepeatBalls)
        }
        break
      case '1':
        var twiceInfoArr = []
        var twiceArrLength = repeatArr.length
        for (var i = 0; i <= twiceArrLength * twiceArrLength; i++) {
          var twiceNumber1 = Math.floor(i / twiceArrLength)
          var twiceNumber2 = i % twiceArrLength
          if (twiceNumber1 <= twiceNumber2 || twiceNumber1 === twiceArrLength)
            continue
          var countArr = []
          countArr.push(repeatArr[twiceNumber1])
          countArr.push(repeatArr[twiceNumber2])
          twiceInfoArr.push(countArr)
        }
        for (var i = 0; i < twiceInfoArr.length; i++) {
          var flagArr = singleArr.slice(0)
          for (var j = 0; j < twiceInfoArr[i].length; j++) {
            flagArr = flagArr.filter(item => item !== twiceInfoArr[i][j])
          }
          count += flagArr.length
        }
        break
      case '2':
        for (var i = 0; i < repeatArr.length; i++) {
          var calculationType2ActualArr = []
          for (var j = 0; j < singleArr.length; j++) {
            if (singleArr[j] !== repeatArr[i][0]) {
              calculationType2ActualArr.push(singleArr[j])
            }
          }
          count += combination(
            calculationType2ActualArr.length,
            +params.noRepeatBalls
          )
        }
        break
      // 针对于快三的二同号玩法算法
      case '3':
        for (var i = 0; i < repeatArr.length; i++) {
          var calculationType3ActualArr = []
          for (var j = 0; j < singleArr.length; j++) {
            if (singleArr[j] !== repeatArr[i][0][0]) {
              calculationType3ActualArr.push(singleArr[j])
            }
          }
          count += combination(
            calculationType3ActualArr.length,
            +params.noRepeatBalls
          )
        }
        break
      default:
        return {
          result: -6,
          message: '未被定义的任选项, methodDetail的下标2配置错误',
          betCount: 0,
          numberStr: '',
          selectLineCounts: '',
          showNumberCountsStr: 0
        }
    }
  }
  if (renxuanInMenuDetail) {
    count = count * combination(rxCounts, renxuanInMenuDetail)
  }
  return {
    result: 0,
    message: '',
    betCount: count,
    selectLineCounts: selectLineCounts,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betData),
    showNumberCountsStr: count
  }
}

// 定位胆类
function checkLotteryCounts_6(params) {
  var betData = params[BET_DATA]
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var selectLineCounts = betData.filter(item => item.length).length
  var count = 0
  if (params.isLetou) {
  } else {
    for (var i = 0; i < betData.length; i++) {
      count += betData[i].length
    }
  }
  return {
    result: 0,
    message: '',
    betCount: count,
    selectLineCounts: selectLineCounts,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betData),
    showNumberCountsStr: count
  }
}

// 胆拖类
function checkLotteryCounts_7(params) {
  var betData = params[BET_DATA]
  var selectLineCounts = betData.filter(item => item.length).length
  if (!betData[0].length || !betData[1].length) {
    return {
      result: 0,
      message: '',
      betCount: 0,
      numberStr: '',
      selectLineCounts: '',
      showNumberCountsStr: 0
    }
  }
  var areaSplitChar = params[AREA_SPLIT_CHAR]
  var ballSplitChar = params[BALL_SPLIT_CHAR]
  var danCounts = params.danCounts

  var danLength = betData[0].length
  var combinationN = danCounts - danLength

  var betCounts = combination(betData[1].length, combinationN)
  return {
    result: 0,
    message: '',
    betCount: betCounts,
    selectLineCounts: selectLineCounts,
    numberStr: sortNumber(areaSplitChar, ballSplitChar, betData),
    showNumberCountsStr: betCounts
  }
}

// 阶乘算法
function factorial(m, n) {
  var num = 1
  var count = 0
  for (var i = m; i > 0; i--) {
    if (count === n) {
      //当循环次数等于指定的相乘个数时，即跳出for循环
      break
    }
    num = num * i
    count++
  }
  return num
}

//就是Cmn(上面是n，下面是m) = Amn(上面是n，下面是m)/Ann(上下都是n)
// C(m, n)算法 m -> 当前选择的球数
// n -> 单注需要的球数
// 3 2
function combination(m, n) {
  if (m === 0) return m
  var result = factorial(m, n) / factorial(n, n)
  return result < 1 ? 0 : result
}

// 利润率算法
function profitRateTracking(trackingInfo) {
  var paramsArr = [
    'price', // 价格系数 ---> 配置文件
    'prize', // 单倍单次最大中奖金额 -----> 配置文件
    'selectedCounts', // 已选注数 --------------> 当前选择的注数
    'coefficient', // 价格档位系数 -----> 圆角分 索引
    'maxMultiple', // 最大倍数 -------> 配置文件
    'maxPrizeGroupKey', // 用户奖金组中最大的key值  -------> 配置文件
    'maxPrizeGroup', // 单倍单注最大中奖金额  --->  配置文件 1970
    'chasingPeriodsCounts', // 追号期数
    'startMultiple', // 起始倍数
    'lowestIncomeRate' // 最低收益率
  ]
  for (var i = 0; i < paramsArr.length; i++) {
    if (trackingInfo[paramsArr[i]] === undefined)
      return {
        result: -1,
        message: '缺少参数' + paramsArr[i],
        trackingData: null
      }
  }
  var maxPrizeGroup = trackingInfo['maxPrizeGroup']

  var chasingPeriodsCounts = +trackingInfo.chasingPeriodsCounts

  var startMultiple = +trackingInfo.startMultiple

  if (startMultiple === 0 || chasingPeriodsCounts === 0)
    return {
      result: -2,
      message: "'startMultiple'或'chasingPeriodsCounts'参数错误",
      trackingData: null
    }

  var lowestIncomeRate = trackingInfo.lowestIncomeRate
  var multiple = startMultiple

  var returnData = []

  for (var j = 0; j < chasingPeriodsCounts; j++) {
    var i = 1
    // 当前投注数
    var allSelectedCounts = trackingInfo.selectedCounts * (j + 1)
    // 当前总投注金额
    var allBetMoney =
      allSelectedCounts * trackingInfo.coefficient * trackingInfo.price
    while (i <= chasingPeriodsCounts) {
      // 奖金
      // const wnMoney = prize * price * coefficient * multiple
      var wnMoney =
        ((maxPrizeGroup * trackingInfo.prize) / trackingInfo.maxPrizeGroupKey) *
        trackingInfo.price *
        trackingInfo.coefficient *
        multiple
      if (trackingInfo.maxMultiple <= multiple) {
        if ((wnMoney - allBetMoney) / allBetMoney < lowestIncomeRate) {
          // 已经不满足对应的追号条件
          returnData = null
          break
        }
      }
      if ((wnMoney - allBetMoney) / allBetMoney < lowestIncomeRate) {
        multiple += 1
        i++
      } else {
        break
      }
    }
    returnData.push(multiple)
  }

  if (returnData && returnData.length) {
    return {
      result: 0,
      message: '',
      trackingData: returnData
    }
  } else {
    return {
      result: -3,
      message: '您设置的参数无法达到预期的盈利, 请重试',
      trackingData: null
    }
  }
}

if (!Array.prototype.find) {
  Object.defineProperty(Array.prototype, 'find', {
    value: function(predicate) {
      // 1. Let O be ? ToObject(this value).
      if (this == null) {
        throw new TypeError('"this" is null or not defined')
      }

      var o = Object(this)

      // 2. Let len be ? ToLength(? Get(O, "length")).
      var len = o.length >>> 0

      // 3. If IsCallable(predicate) is false, throw a TypeError exception.
      if (typeof predicate !== 'function') {
        throw new TypeError('predicate must be a function')
      }

      // 4. If thisArg was supplied, let T be thisArg; else let T be undefined.
      var thisArg = arguments[1]

      // 5. Let k be 0.
      var k = 0

      // 6. Repeat, while k < len
      while (k < len) {
        // a. Let Pk be ! ToString(k).
        // b. Let kValue be ? Get(O, Pk).
        // c. Let testResult be ToBoolean(? Call(predicate, T, « kValue, k, O »)).
        // d. If testResult is true, return kValue.
        var kValue = o[k]
        if (predicate.call(thisArg, kValue, k, o)) {
          return kValue
        }
        // e. Increase k by 1.
        k++
      }

      // 7. Return undefined.
      return undefined
    }
  })
}

if (!Array.prototype.map) {
  Array.prototype.map = function(callback, thisArg) {
    var T, A, k

    if (this == null) {
      throw new TypeError(' this is null or not defined')
    }

    // 1. 将O赋值为调用map方法的数组.
    var O = Object(this)

    // 2.将len赋值为数组O的长度.
    var len = O.length >>> 0

    // 3.如果callback不是函数,则抛出TypeError异常.
    if (Object.prototype.toString.call(callback) != '[object Function]') {
      throw new TypeError(callback + ' is not a function')
    }

    // 4. 如果参数thisArg有值,则将T赋值为thisArg;否则T为undefined.
    if (thisArg) {
      T = thisArg
    }

    // 5. 创建新数组A,长度为原数组O长度len
    A = new Array(len)

    // 6. 将k赋值为0
    k = 0

    // 7. 当 k < len 时,执行循环.
    while (k < len) {
      var kValue, mappedValue

      //遍历O,k为原数组索引
      if (k in O) {
        //kValue为索引k对应的值.
        kValue = O[k]

        // 执行callback,this指向T,参数有三个.分别是kValue:值,k:索引,O:原数组.
        mappedValue = callback.call(T, kValue, k, O)

        // 返回值添加到新数组A中.
        A[k] = mappedValue
      }
      // k自增1
      k++
    }

    // 8. 返回新数组A
    return A
  }
}
